<template>
	<div class="test-component">
		<h2>{{ test.title }}</h2>
		<p>{{ test.description }}</p>
		<form @submit.prevent="submitTest">
			<div v-for="(exercise, index) in test.exercises" :key="exercise.id">
				<div class="card mb-3">
					<div class="card-body">
						<h5 class="card-title">{{ exercise.question }}</h5>
						<div
							v-if="
								exercise.exercise_type ===
								'multiple_choice_multiple_answers'
							"
						>
							<!-- Afiai opiunile pentru gril multipl -->
							<div
								class="form-check"
								v-for="(
									option, optionIndex
								) in exercise.options"
								:key="option.id"
							>
								<input
									class="form-check-input"
									type="checkbox"
									:value="optionIndex"
									v-model="answers[index]"
								/>
								<label class="form-check-label">{{
									option.answer
								}}</label>
							</div>
						</div>
						<div
							v-else-if="
								exercise.exercise_type ===
								'multiple_choice_single_answer'
							"
						>
							<!-- Afiai opiunile pentru gril cu rspuns unic -->
							<div
								class="form-check"
								v-for="(
									option, optionIndex
								) in exercise.options"
								:key="option.id"
							>
								<input
									class="form-check-input"
									type="radio"
									:value="optionIndex"
									v-model="answers[index]"
								/>
								<label class="form-check-label">{{
									option.answer
								}}</label>
							</div>
						</div>
						<div v-else-if="exercise.exercise_type === 'numeric'">
							<!-- Afiai cmpul pentru rspuns numeric -->
							<input
								type="number"
								class="form-control"
								v-model="answers[index]"
							/>
						</div>
						<div v-else-if="exercise.exercise_type === 'ordering'">
							<!-- Afiai elementele pentru ordonare -->
							<div class="form-group">
								<label
									v-for="(
										item, itemIndex
									) in exercise.ordering_items"
									:key="item.id"
								>
									{{ item.item }}
									<select
										class="form-control"
										v-model="answers[index][itemIndex]"
									>
										<option
											v-for="(
												_, i
											) in exercise.ordering_items"
											:value="i + 1"
										>
											{{ i + 1 }}
										</option>
									</select>
								</label>
							</div>
						</div>
						<div
							v-else-if="
								exercise.exercise_type === 'fill_in_the_blank'
							"
						>
							<!-- Afiai cmpurile pentru completarea propoziiei -->
							<div class="form-group">
								<template
									v-for="(
										word, wordIndex
									) in exercise.fill_in_the_blank_items"
								>
									<input
										type="text"
										class="form-control"
										v-model="answers[index][wordIndex]"
									/>
									<span
										v-if="
											wordIndex <
											exercise.fill_in_the_blank_items
												.length -
												1
										"
										>{{ word.word }}
									</span>
								</template>
							</div>
						</div>
					</div>
				</div>
			</div>
			<button type="submit" class="btn btn-primary">
				Trimite testul
			</button>
		</form>
	</div>
</template>
<script>
export default {
	props: {
		test: {
			title: "",
			description: "",
			exercises: [],
		},
	},
	data() {
		return {
			answers: [],
		};
	},
	methods: {
		async submitTest() {
			// Trimite rspunsurile utilizatorului la server
			const response = await fetch("/test/" + this.testId, {
				method: "POST",
				headers: {
					"Content-Type": "application/json",
				},
				body: JSON.stringify(this.answers),
			});
			const result = await response.json();

			// Afiai rezultatul testului (de exemplu, numrul de puncte obinute)
			alert("Ai obinut " + result.points + " puncte!");
		},
	},
};
</script>
